@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  font-size: 16px;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #eeeeee;
}

.input-label-container{position:relative}.input-label-container>input{font-family:inherit;font-size:1rem;padding:.5rem 1rem;width:100%;border-radius:4px;outline:none;border:1px solid #bdbdbd;color:#000}.input-label-container>input:focus{border-color:#f57c01}.input-label-container>input:focus~label{top:-1.2rem;font-size:14px;color:#f57c01}.input-label-container>input[type=file]{background-color:#fff;cursor:pointer;width:auto}.input-label-container>label{pointer-events:none;position:absolute;top:.6rem;left:1rem;transition:300ms ease all;color:#bdbdbd}.input-label-container>p{margin:.5rem 1rem 0 1rem;color:red;font-weight:bold}.input-label-container input[type=number]::-webkit-inner-spin-button,.input-label-container input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.input-label-container input[type=number]{-webkit-appearance:textfield;appearance:textfield;margin:0}.input-label-container input[type=file]{width:100%}.input-label-container input[type=date]~.label,.input-label-container input[type=file]~.label{top:-1.2rem;font-size:14px}.input-label-container>.shrink{top:-1.2rem;font-size:14px}
.btn-container{display:flex;justify-content:flex-end}.btn-container>.save-btn{font-family:inherit;cursor:pointer;padding:.5rem 1rem;color:#fff;border:none;border-radius:4px;background-color:#f57c01}.btn-container>.save-btn:disabled{cursor:default;opacity:.5}
.form{display:flex;flex-direction:column}.form>div{margin:0 0 1rem 0}
.grid-container{display:grid}@media screen and (max-width: 624px){.grid-container{display:flex;flex-direction:column}.grid-container>div{margin-bottom:1rem}div:last-child{margin-bottom:0}}
.select-container{position:relative}.select-container>select{cursor:pointer;font-family:inherit;font-size:1rem;margin:0 0 .5rem 0;padding:.5rem 1rem;width:100%;border-radius:4px;outline:none;background-color:#fff;border:1px solid #bdbdbd}.select-container>select:focus{border-color:#f57c01}.select-container>select:focus~label{top:-1.2rem;font-size:14px;color:#f57c01}.select-container>label{pointer-events:none;position:absolute;top:.6rem;left:1rem;transition:300ms ease all;color:#bdbdbd}.select-container>.shrink{top:-1.2rem;font-size:14px}
.container{width:50vw;margin:1rem auto;background-color:#fff;padding:1rem 1rem 3rem 1rem;border-radius:.5rem}.container .form-title{margin:0 0 2rem 0}.container input{font-size:1rem}@media screen and (max-width: 1220px){.container{width:70vw}}@media screen and (max-width: 864px){.container{width:85vw}}
